<template>
  <div style="height:100%;">
    <div class="flex-between" style="height:100%;">
      <div style="overflow-y:auto;background-color:#f7f8fa;">
        <van-sidebar v-model="goodsCategoryTwoIndex" @change="_choose_goods_category_two">
          <van-sidebar-item v-for="item,index in goodsCategoryTwoList" :key="index" :title="item.cateName" />
        </van-sidebar>
      </div>

      <!-- 商品列表 -->
      <div style="width:calc(100% - 80px);background-color:#f9f9f9;position:relative;">
        <van-loading
          v-if="loadingVisible"
          vertical
          class="flex-center"
          style="position:absolute;z-index:2;left:50%;top:50%;transform:translate(-50%,-50%);width:100%;height:100%;background-color:rgba(255,255,255,.5);"
        >加载中...</van-loading>
        <div
          class="list"
          style="position:ralative;z-index:1;height:100%;overflow-y:auto;"
          @scroll="_on_goods_list_scroll_to_bottom"
          ref="goodsList"
        >
          <div class="item" v-for="(item,index) in goodsList" :key="index" @click="_go_detail(item)">
            <div class="topImage">
            <img :src="$utils.dealImg(item.gimg)" alt @load="_on_img_loaded"/>
            </div>
            <div class="bottomText">
              <p class="title">{{item.gname}}</p>
              <div class="labels">
                <div class="label" v-if="item.gfanj">送{{item.gfanj}}积分</div>
              </div>
              <div class="bottom">
                <div class="left">
                   <div class="price1">
                    <span class="label">优惠价</span>
                    <span class="font">¥</span>
                    <span class="num">{{item.goutPrice?item.goutPrice.toFixed(2):'0.00'}}</span>
                  </div>
                  <div class="price">
                    <span class="label">市场价</span>
                    <span class="font">¥</span>
                    <span class="num">
                      <del>{{item.gmarketPrice?item.gmarketPrice.toFixed(2):'0.00'}}</del>
                    </span>
                  </div>
                 </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getIndexMenu, getIndexGoodsList } from '@/api';
export default {
  data: ()=>{
    return {
      goodsCategoryTwoIndex: 0,
      goodsCategoryTwoList: [], // 二级分类列表
      goodsList: [],
      keyWord: '',

      pageNum: 1, // 当前页数
      pageSize: 10, // 当页条数

      waitLoadMaxTime: 30000, // 等待loading最长时间，单位毫秒
      waitloadTimer: null,
      loadingVisible: false,
      loadedImgNumber: 0, // 加载完成的图片数量
    }
  },
  computed: {
    currCateCode: function(){
      return this.goodsCategoryTwoList[this.goodsCategoryTwoIndex].cateCode;
    }
  },
  async mounted(){
    document.title = '商品分类';
    await this.getGoodsCategoryTwoList();
    this.getGoodsList().then(res=>{
      this.goodsList = res.data.list;
    });
  },
  destroyed(){
    clearTimeout(this.waitloadTimer);
  },
  methods: {

    // 获取产品二级分类列表
    getGoodsCategoryTwoList(){
      return new Promise((resolve,reject)=>{
        getIndexMenu({
          cateCode: '825678A88DCD43528E3268E2F65A045D',
          cateLevel: 2
        }).then(res=>{
          if(res.code === 0){
            this.goodsCategoryTwoList = res.data;
            resolve(res);
          }else{
            this.$toast.fail(res.msg);
            reject(res);
          }
        }).catch(reject)
      })
    },

    // 获取产品列表
    getGoodsList(){

      return new Promise((resolve,reject)=>{
        
        let dataObj = {
          cateCode: this.currCateCode,
          viewName: this.keyWord,
          pageNum: this.pageNum,
          pageSize: this.pageSize,
        };
        this.loadingVisible = true;

        getIndexGoodsList(dataObj).then(res=>{

          if(res.code === 0){

            this.waitloadTimer = setTimeout(()=>{
              this.loadingVisible = false;
              this.loadedImgNumber = 0;
              clearTimeout(this.waitloadTimer);
            },this.waitLoadMaxTime)
            resolve(res);

          }else{

            this.$toast.fail(res.msg);
            this.loadingVisible = false;
            this.loadedImgNumber = 0;
            clearTimeout(this.waitloadTimer);
            reject(res);

          }

        }).catch(reject)
      });

    },
    _choose_goods_category_two(){
      this.pageNum = 1;
      this.$refs.goodsList.scrollTop = 0;
      this.getGoodsList().then(res=>{
        this.goodsList = res.data.list;
      });
    },
    _on_goods_list_scroll_to_bottom(e){
      if(
        e.target.scrollHeight
        - e.target.clientHeight
        - e.target.scrollTop
        === 0
      ){
        this.pageNum++;
        this.getGoodsList().then(res=>{
          if(res.data.list.length > 0){
            this.goodsList = this.goodsList.concat(res.data.list)
          }else{
            this.pageNum--;
          }
        })
      }
    },
    _on_img_loaded(){
      this.loadedImgNumber++;
      // console.log('加载完毕的图片数量:' + this.loadedImgNumber)
      // console.log('当前页的图片数量:' + this.pageSize)
      // console.log('总的图片数量:' + this.goodsList.length);
      // console.log('最后一页的图片数量:' + this.goodsList.length % this.pageSize)
      // console.log('---------------------')
      if(
        this.loadedImgNumber === this.pageSize
        || this.loadedImgNumber === this.goodsList.length % this.pageSize
      ) {
        this.loadingVisible = false;
        this.loadedImgNumber = 0;
        clearTimeout(this.waitloadTimer);
      }
    },
    _go_detail(item) {
      this.$router.push({
        name: "goodsDetail",
        query: { id: item.gcode, title: item.gname },
      });
    },
  }
}
</script>

<style lang="scss" scoped>
  .flex-between {
    display: flex;
    justify-content: space-between;
  }

  .list {
      padding: 10px;
      overflow: hidden;
      .item {
        border-bottom: 1px solid #eee;
        padding: 5px 0;
        align-items: flex-start;
        min-height: 120px;
        display: flex;
        .topImage {
          display: inline-block;
          img {
            width: 110px;
            height:100%;
            // height: calc(120px / 1.316);
          }
        }
        .bottomText {
          display: inline-block;
          .title {
            margin-left: 15px;
            padding-bottom:5px;
            font-size: 14px;
            line-height: 18px;
            max-height: 50px; 
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 2;
            overflow: hidden;
            color: #333;
            letter-spacing: 1px;
          }
          .labels {
            margin-left: 15px;
            margin-top: 10px;
            .label {
              display: inline-block;
              font-size: 11px;
              margin-right: 10px;
              padding: 3px 8px;
              border-radius: 5px;
              border: 1pt solid orange;;
              color: orange;
            }
          }
          .bottom {
            color: $base-color;
            font-size: 12px;
            line-height: 30px;
            width: 100%;
            display: flex;
            align-items: center;
            margin: 2px 0 0;
            padding-top: 10px;
            .left {
              margin-top: -6px;
              display: flex;
           
            .line{
              margin-top: 18px;
              //margin-left: 6px;
              width:93vw;
            }
            .price {
              display: inline-block;
              padding-left: 5px;
              
              .label {
                font-size: 10px;
                 color: #000000;
                background: #dae6fd;
                padding: 3px 5px;
                border-radius: 5px;
                margin-right: 5px;
                box-sizing: border-box;
                border:0;
              }
              .font {
                font-size: 10px;
                 color: #999;
                margin-right: 3px;
              }
              .num {
                font-weight: bold;
                font-size: 12px;
                color: #999;
              }
            }
            .price1 {
              display: inline-block;
              margin-left: 15px;
              .label {
                font-size: 10px;
               color: #f70707;
                border: 1pt solid #f70707;
                padding: 3px 5px;
                border-radius: 5px;
                margin-right: 5px;
                box-sizing: border-box;
              }
              .font {
                font-size: 12px;
               color: #f70707;
              }
              .num {
                font-weight: bold;
                font-size: 12px;
               color: #f70707;
              }
            } }
          }
        }
      }
    }
</style>